.menu {
	margin: 2rem auto;
  
	.slider-nav-button-container {
	  display: flex;
	  flex-direction: column;
	  justify-content: flex-start;
	  align-items: center;
	  gap: 16px;
	  padding-top: 5px;
	  padding-bottom: 10px;

	  @media screen and (max-width: 1100px) {
		display: flex;
	}
	
	  .status-container {
		display: flex;

		h4 {
		  font-size: 20px;
		  font-family: "Poppins", sans-serif;
		  margin: 0 57px 0 0;
		}
  
		form {
		  display: flex;
		  gap: 37px;
  
		  .status-item {
			label {
			  width: auto;
			  font-size: 20px;
			  cursor: pointer;
			  display: flex;
			  gap: 14px;
			  place-items: center;
  
			  input[type="radio"] {
				width: 17px;
				height: 17px;
				appearance: none;
				background-color: #fff;
				margin: 0;
				font: inherit;
				border: 0.15em solid white;
				border-radius: 50%;
				display: grid;
				place-content: center;
				cursor: pointer;
			  }
  
			  input[type="radio"]::before {
				content: "";
				width: 0.5em;
				height: 0.5em;
				border-radius: 50%;
				transform: scale(0);
				transition: 200ms transform ease-in-out;
				box-shadow: inset 1em 1em var(--neon-purple);
			  }
  
			  input[type="radio"]:checked::before {
				transform: scale(1);
			  }
			}
		  }
		}
	  }
  
	  .job-title-container {
		display: flex;
  
		h4 {
		  font-size: 20px;
		  min-width: fit-content;
		  font-family: "Poppins", sans-serif;
		  margin: 0 33px 0 0;
		}
  
		.category-items {
		  display: flex;
		  flex-wrap: wrap;
		  font-size: 20px;
		  text-transform: uppercase;
  
		  .category-item {
			border-left: 1px solid grey;
			cursor: pointer;
			padding: 0 18px;
  
			&:hover {
			  color: var(--neon-purple);
			}
		  }
		}
	  }
  
